@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@100&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@100;400&display=swap");
ul {
  padding-inline-start: 0px;
}
body {
  background-color: beige;
}

div.app {
  width: 100%;
}

div.header {
  color: blue;
  text-align: center;
  margin: 5px;
  font-family: "Montserrat", sans-serif;
}

header {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 30px 10%;
}

.navcontact {
  list-style: none;
  padding: 10px;
  margin: 5px 100px 5px 100px;
}

.navcontact li {
  display: inline-block;
  list-style: none;
  padding: 0px 10px;
}

.navcontact li a {
  transition: all 0.3s ease 0s;
  font-family: "Montserrat", sans-serif;
  font-weight: 500;
  font-size: 16px;
  text-decoration: none;
}

.navcontact li a:hover {
  color: red;
}

.rescontainer {
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
  max-width: 960px;
  margin: 0 150px 0px 150px;
  
}

.edwork{
  margin: 20px;
}

.edwork li {
  list-style: none;
  list-style-type: none;
  padding: 0px 10px;
  text-decoration: none;
  line-height: 1.5;
  margin-left: 0px;
  text-align: center;
  font-family: "Montserrat", sans-serif;
  font-size: 14px;
}

.aboutme {
  margin: 20px;
}
.careergoal {
  margin: 20px;
}

.rescontainer-2 {
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
  max-width: 800px;
  margin: 0 150px 0px 150px;
}
.aboutme {
  width: 300px;
}
.careergoal {
  width: 300px;
}

.skills {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
}

p {

  line-height: 1.5;
  text-align: center;
  font-family: "Montserrat", sans-serif;
}
h1 {
  text-align: center;
  font-family: "Montserrat", sans-serif;
}



